import React from 'react';
import core from '../core/Core';
import data from '../core/Data';
import lang from '../core/i18n';
const AlertNoticeInapp = ({
    openParam
}: {
    openParam: any
}) => {
    let price = '';
    let inappInfoArr = data.backInappInfo;
    let locale;
    let printPrice = '';
    if (inappInfoArr && inappInfoArr.length > 0) {
        const arr = inappInfoArr[0].split('#')
        price = arr[2];
        locale = arr[3];
        locale = locale.replace(/ \(fixed\)/, '');
        locale = locale.substring(locale.length - 3);

        if (locale === "KRW") {
            printPrice = `(${data.numberWithCommas(parseInt(price)) + "원"})`;
        } else {
            printPrice = `(${locale + price})`;
        }
    }




    return (
        <div className="AlertNoticeInapp">
            <div className="message">
                {data.todayFreeCntDefault}{lang.t('inapp-alert1')}<br></br>
                {lang.t('inapp-alert2')}<br></br>
                {lang.t('inapp-alert3')}<br></br>
                Only {printPrice}
            </div>

            <button className="btn-confirm" onClick={() => {
                core.hideAlert();
                core.insertPage("PageMenu");
            }}>{lang.t('inapp-alert-btnok')}</button>
            <button className="btn-close" onClick={() => {
                core.hideAlert();
            }}>{lang.t('inapp-alert-btnno')}</button>
        </div>
    );
};

export default AlertNoticeInapp;