import classnames from 'classnames';
import { css } from 'emotion';
import React, { useEffect } from 'react';
import { useRecoilValue } from 'recoil';
import core from '../core/Core';
import data from '../core/Data';
import { cidState } from '../core/store';
import WCourse from './WCourse';
import { Tags } from './WMain';
import fbase from '../fb/fbase';

const selfName = "WCourseMain";
const WCourseMain = () => {
    const cid = useRecoilValue(cidState);
    useEffect(() => {

    }, [cid])
    const leftJSONFilter = data.leftJSON.filter((item: any, i: number) => {
        return item.courseID === cid;
    })
    const courseItemListJSON = leftJSONFilter[0];
    const isOwner = fbase.userMoreInfo.buyCourses.includes(courseItemListJSON.courseID);
    return (
        <div className="WCourseMain">

            <div className="contents">
                <div className="course-item">
                    <div className={classnames(["cover-img", css`
                        background-image: url("${data.getRootPath()}resource/courses/${courseItemListJSON.courseID}/cover.jpg");
                    `])}>
                    </div>
                    <div>
                        <h3>{courseItemListJSON.title}</h3>
                        <div>{courseItemListJSON.item.length}개 코스</div>
                        <div className="tags">
                            <Tags str={courseItemListJSON.tags} key={courseItemListJSON.courseID} />
                        </div>
                        {
                            isOwner
                                ?
                                <div>구매완료</div>
                                :
                                <button>구매 1,300원</button>
                        }

                    </div>
                </div>
                <div className="list-header">53개 코스</div>
                {cid && <WCourse json={courseItemListJSON} parentName={selfName} />}
            </div>

        </div>
    );
};

export default WCourseMain;