import CryptoJS from 'crypto-js';
import { createBrowserHistory } from "history";
import data from './Data';
import EventEmitter, { Event } from './EventEmitter';

let scrollingTick: any = null;
let scrollingLazyTime: number = 0;
const key = "XDVl1SGxRn";
class PlayHotClip {
    thumbJSON: any = null;// hotclip json
    topicJSON: any = null;// 해당ID의 FULL토픽
    playClipIdx: number = 0;


    setData(json: any, inCallback: () => void) {
        this.playClipIdx = 0;
        this.thumbJSON = json;
        data.getTopicData(json.topicID, (topicJSON: any) => {
            // 숨김문자 생성(여기서 생성 해서 리렌더링시에 다시 하지 않도록 한다)
            this.topicJSON = data.makeMoreInfoJSON(topicJSON);
            // console.log('getTopicData sss', currJSON);
            data.initAudio(this.topicJSON);

            setTimeout(() => {
                inCallback();
            });

        });
        // this.currJSON = data.makeMoreInfoJSON(json);
        // data.initAudio(json);

    }
    playThumb(inCallback: (state: string) => void) {
        if (!this.topicJSON) return;
        // 1번 문장 플레이
        data.audioPlayPause(this.thumbJSON.sentense[0].idx, () => {
            // 2번 문장 플레이
            inCallback('0');
            data.audioPlayPause(this.thumbJSON.sentense[1].idx, () => {
                // 끝
                inCallback('1');
            });
        });
    }
}
const playHotClip = new PlayHotClip();

class Core {

    history = createBrowserHistory();
    isSentensePressing: boolean = false;

    constructor() {
        this.addEvent();
        setTimeout(() => {
            console.log('first check changePageForHistory')
            this.changePageForHistory();
        }, 500)
    }

    addEvent() {
        this.history.listen(({
            location,
            action
        }: {
            location: any,
            action: any,
        }) => {
            console.log('location', location, action)
            if (action === 'POP') {
                this.changePageForHistory();
            }
        });
    }


    setParameter(paramD: string) {

        if (paramD === '') {
            this.history.push("");
            window.location.href = '/';
        } else {
            // this.history.push(`?d=${paramD}`);
            window.location.href = `?d=${paramD}`;
        }

        this.changePageForHistory();
        // EventEmitter.dispatch(Event.CHANGE_HISTORY, {});
        // Object.assign(queryStringObj, {param: paramName});
        // EventEmitter.dispatch(Event.CHANGE_HISTORY, queryStringObj);
    }

    changePageForHistory() {
        const search = (window as any).location.search;
        console.log('decp search: ', search)
        const d = search.substring(3);// ?d=을 제외한 나머지
        console.log('decp d: ', d)
        const decrypt = this.decryptURL(d);
        if (decrypt && decrypt.tpid) {
            console.log('decp : ', decrypt)
            data.tpid = decrypt.tpid;
            data.cid = decrypt.cid;
            EventEmitter.dispatch(Event.CHANGE_TPID, {
                tpid: decrypt.tpid,
                cid: decrypt.cid,
            });
        } else {
            if (search.substr(0, 3) === '?d=') {
                alert('wrong url');
            } else {
                console.log('correct case');
            }
            console.log('decp : undefined tpid')

            EventEmitter.dispatch(Event.CHANGE_TPID, {
                tpid: ''
            });
        }
        // console.log('ueryString.parse(search)',this.decryptURL(d).tpid);
        // alert((window as any).history.search);
    }

    decryptURL(text: string) {
        try {
            text = text.substring(7);
            const bytes = CryptoJS.AES.decrypt(text, key);
            return JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
        } catch (err) {
            // console.error(err);
            return;
        }
    }

    insertPage(pageID: string) {
        EventEmitter.dispatch(Event.INSERT_PAGE, {
            pageID: pageID
        });
    }
    replacePage(pageID: string) {
        EventEmitter.dispatch(Event.REPLACE_PAGE, {
            pageID: pageID
        });
    }
    gotoBack(idx: number) {
        EventEmitter.dispatch(Event.GOTO_BACK, {
            idx: idx
        });
    }
    resetPage(arr: string[]) {
        EventEmitter.dispatch(Event.RESET_PAGE, {
            arr: arr
        });
    }
    showAlert(message: string) {
        EventEmitter.dispatch(Event.SHOW_ALERT, {
            moduleName: 'AlertBasic',
            message: message,
        });
    }
    showAlertModule(moduleName: string) {
        EventEmitter.dispatch(Event.SHOW_ALERT, {
            moduleName: moduleName
        });
    }
    hideAlert() {
        EventEmitter.dispatch(Event.HIDE_ALERT, {});
    }
    showBuyMessage({
        courseID
    }: {
        courseID: string
    }) {

        EventEmitter.dispatch(Event.CHANGE_BTN_PLAY_STATE, "stop");
        EventEmitter.dispatch(Event.SHOW_ALERT, {
            moduleName: "AlertNoticeInapp",
            courseID: courseID,
        })
    }
    buyProduct({
        courseID
    }: {
        courseID: string
    }) {

        EventEmitter.dispatch(Event.CHANGE_BTN_PLAY_STATE, "stop");
        EventEmitter.dispatch(Event.SHOW_ALERT, {
            moduleName: "AlertBuyProduct",
            courseID: courseID,
        })
    }
    showToastMessage(options: {
        message: string,
        mtime: number
    }) {
        EventEmitter.dispatch(Event.SHOW_TOAST_MESSAGE, options)
    }
    playHotclipThumb(json: any, inCallback: (state: string) => void) {
        playHotClip.setData(json, () => {
            playHotClip.playThumb(inCallback);
        });

    }
    playHotclipDetail(json: any) {

    }
    // 현재 화면이 스크롤링 중인가?
    isScrolling(callback: (result: boolean) => void) {
        const firstY = window.scrollY;
        setTimeout(() => {
            if (firstY === window.scrollY) {
                callback(false);
            } else {
                callback(true);
            }
        }, 100);
    }
    // sentense클릭동안 스크롤락 실행
    sentensePressing() {
        this.isSentensePressing = true;
        scrollingLazyTime = 2000;// 2초
        if (scrollingTick) {
            clearInterval(scrollingTick);
        }
        scrollingTick = setInterval(() => {
            scrollingLazyTime = scrollingLazyTime - 500;
            if (scrollingLazyTime <= 0) {
                this.isSentensePressing = false;
                scrollingLazyTime = 0;
                clearInterval(scrollingTick);
            }
        }, 500);
    }
    scrollTo(posY: number, selector?: string) {
        let isEnterFrame = true;
        console.log('tmplog_posY', posY)
        const $target: any = (selector) ? document.querySelector(selector) : window;
        // $target.scrollTo(0, posY);
        // return;

        function enterFrame() {
            // 이동위치 = 현재 위치 + (목표 위치 - 현재 위치) * 속도
            const nextY = window.scrollY + (posY - window.scrollY) * 0.23;
            $target.scrollTo(0, nextY);

            const scrollY = (selector) ? $target.scrollTop : window.scrollY;

            const math = Math.abs(scrollY - posY);
            if (math < 5) {
                isEnterFrame = false;
            }
            if (isEnterFrame) {
                window.requestAnimationFrame(enterFrame);
            }
        }
        setTimeout(() => {
            isEnterFrame = false;
        }, 2000);
        window.requestAnimationFrame(enterFrame);

    }


}

export default new Core();