import classnames from 'classnames';
import { css } from 'emotion';
import React, { useEffect, useState } from 'react';
import { useSetRecoilState } from 'recoil';
import Equalizer from './component/Equalizer';
import MDictation from './component/MDictation';
import appBridge from './core/AppBridge';
import data from './core/Data';
import lang from "./core/i18n";
import { isShowShadowingState } from './core/store';

const selfName = 'PageShadowing';
let temprepeatMode: any;
const PageShadowing = () => {
    const setIsShowShadowing = useSetRecoilState(isShowShadowingState);
    useEffect(() => {
        temprepeatMode = appBridge.settingInfo.repeatMode;
        appBridge.settingInfo.repeatMode = '1';
        data.setCurrPlayBtnState('play');
        data.audioPlayPause(data.pageShadowingParam.sentenseIdx);

        // 아이폰에서 푸터가 안나타나서 강제로 스크롤한번
        (window as any).scrollTo(0, 10);
        setTimeout(() => {
            (window as any).scrollTo(0, 0);
        }, 1000)

        return () => {
            data.setCurrPlayBtnState('stop');
        }
    }, []);
    return (
        <div className="view-vv view-PageShadowing">
            {/* <PageHeader parentSelf={selfName} /> */}
            {/* <h2>Shadowing & Dictation</h2> */}
            <button onClick={() => {
                setIsShowShadowing(false);
            }} className="btn btn-close">
                <i className="fas fa-times"></i>
            </button>
            <MDictation
                sentenseEn={data.pageShadowingParam.sentenseEn}
                sentenseKo={data.pageShadowingParam.sentenseKo}
            />
            <ControlBtns />
        </div>
    );
};

const ControlBtns = () => {
    const [isPlay, setIsPlay] = useState(true);
    const [interval, setInterval] = useState(0);
    const [speed, setSpeed] = useState(1);
    data.playerSetting.interval = interval;
    data.playerSetting.speed = speed;
    const [isCheck, setIsCheck] = useState((() => {
        return appBridge.settingInfo.keepSentense.includes(`${data.pageShadowingParam.topicID}_${data.pageShadowingParam.sentenseIdx}`);
    })());

    const checkClick = (tf: boolean) => {
        setIsCheck(tf);

        const makeKey = `${data.pageShadowingParam.topicID}_${data.pageShadowingParam.sentenseIdx}`;

        data.setSettingKeepClipSentense(
            'sentense',
            tf ? 'add' : 'remove',
            makeKey
        )
        // 문장을 저장
        data.setSettingKeepSentenseMoreInfo(
            tf ? 'add' : 'remove',
            makeKey,
            data.pageShadowingParam.sentenseEn
        );

    }
    return (
        <div className={`func-setting ${lang.language}`}>
            <div className="row">
                <div className="col1">{lang.t('speed')}</div>
                <div className="col2">
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: speed === 1 }])}
                        onClick={() => {
                            setSpeed(1);
                        }}>x1</button>
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: speed === 0.9 }])}
                        onClick={() => {
                            setSpeed(0.9);
                        }}>x0.9</button>
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: speed === 0.8 }])}
                        onClick={() => {
                            setSpeed(0.8);
                        }}>x0.8</button>
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: speed === 0.7 }])}
                        onClick={() => {
                            setSpeed(0.7);
                        }}>x0.7</button>
                </div>
            </div>
            <div className="row">
                <div className="col1">{lang.t('interval')}</div>
                <div className="col2">
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: interval === 0 }])}
                        onClick={() => {
                            setInterval(0);
                        }}>0s</button>
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: interval === 1 }])}
                        onClick={() => {
                            setInterval(1);
                        }}>1s</button>
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: interval === 1.5 }])}
                        onClick={() => {
                            setInterval(1.5);
                        }}>1.5s</button>
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: interval === 2 }])}
                        onClick={() => {
                            setInterval(2);
                        }}>2s</button>
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: interval === 2.5 }])}
                        onClick={() => {
                            setInterval(2.5);
                        }}>2.5s</button>
                    <button
                        className={classnames(["btn btn-blank btn-fc",
                            { active: interval === 9999 }])}
                        onClick={() => {
                            setInterval(9999);
                        }}>{lang.t('sentenselength')}</button>
                </div>
            </div>


            <div className="footer">
                {
                    isPlay && <button className="btn btn-play" onClick={() => {
                        data.setCurrPlayBtnState('stop', 'a1');
                        appBridge.settingInfo.repeatMode = temprepeatMode;
                        setIsPlay(false);
                    }}><Equalizer /><span className="txt">Stop</span></button>
                }
                {
                    !isPlay && <button className="btn btn-play" onClick={() => {
                        temprepeatMode = appBridge.settingInfo.repeatMode;
                        appBridge.settingInfo.repeatMode = '1';
                        data.setCurrPlayBtnState('play');
                        data.audioPlayPause(data.pageShadowingParam.sentenseIdx);
                        setIsPlay(true);


                    }}><i className="fas fa-retweet"></i><span className="txt">Play</span></button>
                }

                <button className="btn btn-bookmark">
                    {isCheck
                        ?
                        <i className="fas fa-bookmark checkbox check" onClick={() => {
                            checkClick(false);
                        }}><span className="i-label">{lang.t('saved-sentense')}</span></i>
                        :
                        <i className="far fa-bookmark checkbox" onClick={() => {
                            checkClick(true);
                        }}><span className="i-label">{lang.t('save-sentense')}</span></i>
                    }
                </button>
            </div >

        </div >
    );
}

export default PageShadowing;