import React, { Component, createRef, RefObject } from 'react';
import EventEmitter, { Event } from '../core/EventEmitter';
import data from '../core/Data';
import appBridge from '../core/AppBridge';
interface IProps {

}
class AudioPlayer extends Component<IProps> {
    state = {
        id: 'blank',
        path: '../../resource/topics/',
    }
    isInitLoadingAudio: boolean = false;// 네트웍에서는 자동플레이를 최초에 실행안하면 뭔가 로드가 안되는듯하여 자동플레이로하고 첫 업데이트에 멈춰놓는다.
    isInitLoadingAudioCallback: () => void = () => { };
    refAudio: RefObject<HTMLAudioElement> = createRef();
    elAudio: HTMLAudioElement | null = null;
    isAudioAttachEvent: boolean = false;// audio이벤트를 한번만 add하기위한 변수 
    currRealStatus: 'play' | 'pause' | 'stop' = 'stop';// timeupdate경우는 pause후에도 이벤트가 실행되므로 세밀한 제어를 위한 값 추가 
    currStartTimeOri: number = 9999999999;
    currEndTimeOri: number = 9999999999;
    currEndTime: number = 9999999999;
    currCallback: () => void = () => {

    };
    constructor(props: IProps) {
        super(props);
        EventEmitter.subscribe(Event.INIT_AUDIO, (json: any) => {

            // console.log('soundInit', json);
            if (this.elAudio) {
                this.elAudio.volume = 0;
            }
            // 초기화
            this.isInitLoadingAudio = false;// 네트웍에서는 자동플레이를 최초에 실행안하면 뭔가 로드가 안되는듯하여 자동플레이로하고 첫 업데이트에 멈춰놓는다.
            this.isInitLoadingAudioCallback = () => { };
            // alert('initAudio')
            this.setState({
                ...this.state,
                path: '../../resource/topics/',
                id: json.id,
            });
        });
        EventEmitter.subscribe(Event.AUDIO_PLAY_PAUSE, (option: any) => {
            console.log('audioPlayPause', option);

            this.audioPlayPause(
                option.startTime,
                option.endTime,
                option.callback
            )
        });
        EventEmitter.subscribe(Event.AUDIO_STOP, () => {
            this.audioPause();
        });
        // ((document as any).querySelector('.mainaudioch') as any).addEventListener('load', () => {
        //     alert('loadcomplete')
        // });

    }
    componentDidMount() {
        setTimeout(() => {
            // this.audioPlay();
            // this.audioSeek(5000);
        }, 1000);
        this.elAudio = this.refAudio.current;
        if (this.elAudio) {
            this.elAudio.addEventListener('load', () => {
                // alert('load')
            })
            // this.elAudio.play();
        }

    }
    componentDidUpdate() {
        this.elAudio = this.refAudio.current;
        if (this.elAudio && !this.isAudioAttachEvent) {

            this.elAudio?.addEventListener('timeupdate', () => {
                if (!this.isInitLoadingAudio) {
                    this.isInitLoadingAudio = true;
                    this.elAudio?.pause();
                    // alert('this.isInitLoadingAudio')
                    this.isInitLoadingAudioCallback();
                    return;
                }

                const currTime = this.elAudio?.currentTime;

                if (currTime === undefined) return;
                if (this.currEndTime < currTime) {

                    if (this.currRealStatus === 'pause') return;
                    const currentSoundTime = (this.currEndTimeOri - this.currStartTimeOri);

                    // 셋팅 인터벌을 문장길이로 했으면
                    const intervalTime = (() => {
                        const settingInterval = data.playerSetting.interval;
                        if (settingInterval === 9999) {
                            return currentSoundTime + 2000;// 문장길이 +1초 
                        } else {
                            return settingInterval * 1000;// 설정타임
                        }
                    })();

                    this.audioPause();
                    setTimeout(() => {

                        // 다음 플레이를 수동으로 한 경우 callback 안함 
                        if (this.currRealStatus === 'pause') {
                            this.currCallback();
                        }

                    }, intervalTime)

                }
            });
            this.isAudioAttachEvent = true;
        }

    }
    audioPlayPause(startTime: number, endTime: number, callback: () => void) {
        this.currStartTimeOri = startTime;
        this.currEndTimeOri = endTime;
        this.currEndTime = endTime / 1000;
        this.currCallback = callback;
        // console.log('audioSeek', startTime)
        this.audioSeek(startTime);
    }
    audioPlay(time: number | null = null) {

        // 모바일기기의 퍼미션처럼 초기화가 안된경우 초기화 후 다시실행 
        if (!this.isInitLoadingAudio) {
            this.isInitLoadingAudioCallback = () => {
                this.audioPlay(time);
            }
        }

        if (this.elAudio) {
            this.elAudio.volume = 1;
            this.elAudio.playbackRate = data.playerSetting.speed;
            if (time !== null) {
                this.elAudio.currentTime = time / 1000;
            }
            this.currRealStatus = 'play';
            // alert(this.elAudio.currentTime);
            // 카운팅 & 무료체크 & 안내 
            if (!appBridge.addPlayCnt()) return;




            var playPromise = this.elAudio.play();

            if (playPromise !== undefined) {
                playPromise
                    .then(_ => {
                        // Automatic playback started!
                        // Show playing UI.
                        console.log("audio played auto");
                    })
                    .catch(error => {
                        // Auto-play was prevented
                        // Show paused UI.
                        console.log("playback prevented");
                    });
            }
        }

    }
    audioPause() {
        this.currRealStatus = 'pause';
        this.elAudio?.pause();
    }
    audioSeek(time: number) {

        if (this.elAudio) {
            // alert('a2' + (time / 1000));
            this.elAudio.currentTime = time / 1000;
            this.elAudio.playbackRate = data.playerSetting.speed;
            this.audioPlay(time);

        } else {
            // alert('a3')
            // 오디오 준비 전이면 잠시 기다렸다 다시 실행
            setTimeout(() => {

                this.audioSeek(time);

            }, 100);
        }

        // player.duration
        // player.volume
        // player.paused
        // player.currentTime
        // player.addEventListener('timeupdate', updateProgress);
        // player.addEventListener('volumechange', updateVolume);
        // player.addEventListener('loadedmetadata', (
        // player.addEventListener('canplay', makePlay);
        // player.addEventListener('ended', function(){
    }
    render() {

        return (
            <div>

                <audio
                    className="mainaudioch"
                    id="mainaudioch"
                    ref={this.refAudio}
                    src={`${data.getRootPath()}resource/courses/${data.cid}/topics/${this.state.id}/mm.mp3`}

                ></audio>
            </div>
        );
    }
}

export default AudioPlayer;