import React, { useEffect, useState } from 'react';
import AlertBasic from './component/AlertBasic';
import EventEmitter, { Event } from './core/EventEmitter';
import AlertNoticeInapp from './component/AlertNoticeInapp';
import AlertBuyProduct from './component/AlertBuyProduct';
import { useRecoilState, useSetRecoilState } from 'recoil';
import { sentenseRepeatIdxState } from './core/store';
import data from './core/Data';

let openParam: any = null;
const Alert = () => {
    const [moduleName, setModuleName] = useState('');
    // const [updatePagePlaylist, setUpdatePagePlaylist] = useRecoilState(updatePagePlaylistState);
    const setSentenseRepeatIdxState = useSetRecoilState(sentenseRepeatIdxState);
    useEffect(() => {
        EventEmitter.subscribe(Event.SHOW_ALERT, (options: any) => {
            openParam = options;
            setModuleName(options.moduleName);
            if (options.moduleName === 'AlertNoticeInapp') {
                // setUpdatePagePlaylist(updatePagePlaylist + 1);
                setSentenseRepeatIdxState(9999);
                data.setCurrPlayBtnState('stop');
            }

        });
        EventEmitter.subscribe(Event.HIDE_ALERT, (options: any) => {
            setModuleName('');

        });
    }, [])

    const AlertModule = (() => {

        switch (moduleName) {
            case 'AlertBasic':
                return <AlertBasic key="AlertBasic" />;
                break;
            case 'AlertNoticeInapp':
                return <AlertNoticeInapp key="AlertNoticeInapp" openParam={openParam} />;
                break;
            case 'AlertBuyProduct':
                return <AlertBuyProduct key="AlertBuyProduct" openParam={openParam} />;
                break;

        }
    })();
    return (
        <>
            {
                moduleName !== '' && <div className="view-Alert">
                    {AlertModule}
                </div>
            }
        </>
    );
};

export default Alert;


