import core from './core/Core';
import data from './core/Data';
import EventEmitter, { Event } from './core/EventEmitter';
import fbase from './fb/fbase';
// import { lang } from 'moment';
// import lang from "./core/i18n";
import {
    btnRepeatModeUpdateState,
    isCloseDetailPanelState,
    isShowShadowingState,
    sentenseRepeatIdxState,
    tpidState,
    cidState
} from "./core/store";
import LoadingDom from './component/LoadingDom';

let getCourceItemToTopicID: any = null;
let currJSON: any = null;
let callModeChange: () => void;
const sentenseModeCode = ['00', 'e0', '0k', 'ek']; // 문장모드 순번
let sentenseModeIdx: number = 3;
let scriptMode = 'ek';

const selfName = 'Topic';

const PagePlaylist = ({
    isShowPlayerControl,// 콘트롤러가 fixed라서 그다음뷰에서도 보이므로 스택의 마지막뷰가 PagePlaylist인지 체크
}: {
    isShowPlayerControl: boolean,
}) => {

    const [isDataAble, setIsDataAble] = useState(false);
    // const update = useRecoilValue(updatePagePlaylistState);

    useEffect(() => {

        const topicID = data.currJSON.id;
        sentenseModeIdx = 3;
        scriptMode = 'ek';// 최초 코드
        getCourceItemToTopicID = data.getCourceItemToTopicID(topicID);
        currJSON = data.currJSON;

        // console.log('currJSONcurrJSON', currJSON)

        data.initAudio({
            id: topicID
        });


        // 이게먼저호출될지 userInfo가 먼저로드될지 몰라 아래처럼 구성 
        data.setClipHistory(topicID);
        // firebase데이터 로드해서 구성완료 후에 topic history Sync
        EventEmitter.subscribe(Event.ON_AUTH_STATE_CHANGED, () => {
            console.log('docs.data()-2');
            data.setClipHistory(topicID);
        });

        setIsDataAble(true);
        // tag_removega
        data.ga({
            eventCategory: selfName, //서브화면명
            eventAction: 'initClip-' + topicID, //이벤트명
            eventLabel: '',
            eventValue: '',
        });

        EventEmitter.subscribe(Event.SENTENSE_PLAYING, (idx: number) => {

            // 화면 프레싱중이 아닌경우
            if (!core.isSentensePressing) {
                // 스크롤링 중 아닌경우
                core.isScrolling((result: boolean) => {
                    // SentenseItem num-${idx}
                    const $el = (document as any).querySelector(`.SentenseItem.num-${idx}`);
                    const y = ($el) ? $el.offsetTop : null;
                    if (y === null) return;
                    // if (!result) core.scrollTo(y, '.view-Topic');
                    if (!result) {
                        animateScrollTo(y - 200, {
                            elementToScroll: (document as any).querySelector('html')
                        });
                    }

                })
            }


        });
        EventEmitter.subscribe(Event.SENTENSE_PAUSE, (idx: number) => {

        });




        return () => {
            EventEmitter.remove(Event.SENTENSE_PAUSE)
            data.cleanAudio();
            data.setCurrPlayBtnState('stop', 'a6');
        }
    }, []);


    setTimeout(() => {
        // 즐겨찾기에서 온 경우
        if (data.tempGotoSavedSentenseIdx !== 9999) {

            const $el = (document as any).querySelector(`.SentenseItem.num-${data.tempGotoSavedSentenseIdx}`);
            const y = ($el) ? $el.offsetTop : null;

            if (y === null) return;

            animateScrollTo(y - 200, {
                elementToScroll: (document as any).querySelector('.view-Topic')
            });
            // 초기화
            data.tempGotoSavedSentenseIdx = 9999;
        }
    }, 1000)

    return (
        <>
            {
                isDataAble
                    ?
                    <div className="view-vv view-Topic">
                        {/* <PageHeader parentSelf={selfName} /> */}
                        <TopicCover topicID={data.currJSON.id} />

                        <div className="container-playlist">
                            <PlayLists />
                            <GotoFullScript topicID={data.currJSON.id} />
                        </div>

                        {/* <NotiMessage /> */}
                        {isShowPlayerControl && <PlayerControl topicID={data.currJSON.id} />}
                    </div>
                    :
                    <LoadingDom />
            }
        </>
    );
};

export default PagePlaylist;

