const PlayLists = () => {
    useEffect(() => {
        EventEmitter.subscribe(Event.SENTENSE_PLAYING, (idx: number) => {
            setActiveIdx(idx);
        });
        EventEmitter.subscribe(Event.SENTENSE_PAUSE, (idx: number) => {
            setActiveIdx(-1);
        });
        return () => {
            EventEmitter.remove(Event.SENTENSE_PLAYING);
            EventEmitter.remove(Event.SENTENSE_PAUSE);
        };
    }, []);

    const isCloseDetailPanel = useRecoilValue(isCloseDetailPanelState);
    const [activeIdx, setActiveIdx] = useState(-1);
    const sentense = data.currJSON.json.sentense;
    const Component = sentense.map((item: any, i: number) => {
        return <SentenseItem
            idx={i}
            isActive={activeIdx === i}
            json={item}
            key={`sentense${i}`}
        />;
    });
    // console.log('tmplog_fbase.userMoreInfo', fbase.userMoreInfo)
    const [forceRender, setForceRender] = useState(0);
    callModeChange = () => {
        setForceRender(forceRender + 1);
    }
    return (
        <div className={classnames(["PlayLists", css`
            padding-bottom:${isCloseDetailPanel ? 30 : 267}px;
        `])}>
            {Component}
        </div>
    )
}
//['_k', 'ek', 'e0', '0k', '00'];
let temprepeatMode: 'ALL' | '1' | '' | 'nil' = 'nil';
const SentenseItem = ({

    isActive = false,
    json,
    idx,
}: {

    isActive: boolean,
    json: any,
    idx: number,
}) => {

    const [sentenseRepeatIdx, setSentenseRepeatIdxState] = useRecoilState(sentenseRepeatIdxState);
    const clipID: string = currJSON.id;
    const [isGlassMode, setIsGlassMode] = useState(false);
    const isCheckVal = fbase.userMoreInfo.keepSentense.includes(`${clipID}_${idx}`);
    const [isCheck, setIsCheck] = useState(fbase.userMoreInfo.keepSentense.includes(`${clipID}_${idx}`));
    // // console.log('tmplog_idxidx', idx, isCheckVal, `${clipID}_${idx}`, isCheck, fbase.userMoreInfo.keepSentense);
    // 상세 트레이닝 의 북마크 조작 후 리스트로 돌아올때 갱신 안되는 현상 수정

    if (isCheckVal !== isCheck) {
        setTimeout(() => {
            setIsCheck(isCheckVal);
        });
    }
    const [isOpenDesc, setIsOpenDesc] = useState(false);
    const setIsShowShadowing = useSetRecoilState(isShowShadowingState);

    const words = json.words;

    // const [isRepeat, setIsRepeat] = useState(false);
    const checkClick = (tf: boolean) => {
        if (!fbase.isLogin()) {
            alert('로그인이 필요한 서비스에요.')
            return;
        }
        setIsCheck(tf);

        const makeKey = `${clipID}_${idx}`;

        data.setSettingKeepClipSentense(
            'sentense',
            tf ? 'add' : 'remove',
            makeKey
        )
        // 문장을 저장
        data.setSettingKeepSentenseMoreInfo(
            tf ? 'add' : 'remove',
            makeKey,
            json.en
        );

    }
    const playOneItem = () => {
        if (temprepeatMode !== 'nil') {
            fbase.userMoreInfo.repeatMode = temprepeatMode;
            temprepeatMode = 'nil';
        }

        data.setCurrPlayBtnState('stop', 'a5');
        setSentenseRepeatIdxState(9999);
        data.audioPlayPause(idx);
    }
    return (
        <div onTouchStart={() => {
            core.sentensePressing();
        }} className={classnames([`SentenseItem num-${idx}`, { active: isGlassMode }, { active: isActive }])}>

            <div className={classnames(["item-sound", { active: isActive }])}>
                <div className="item-main">
                    <div className="col-left" onClick={() => {
                        core.sentensePressing();
                        data.setCurrPlayBtnState('stop', 'a4');// 현재 실행되고 있는 경우 하단 메인 컨트롤을 멈추기위해서
                        setTimeout(() => {
                            // 1개 반복
                            playOneItem();
                        })

                    }}>

                        {
                            isGlassMode
                                ?
                                <>
                                    <div className="title-en"><ItemCnt idx={idx} /> {json.en}</div>
                                    {data.isSupportLang() && <div className="title-ko">{json.ko}</div>}
                                </>
                                :
                                <>
                                    {['ek', 'e0'].includes(scriptMode) && <div className="title-en"><ItemCnt idx={idx} /> {json.en}</div>}
                                    {['_k'].includes(scriptMode) && <div className="title-en_"><ItemCnt idx={idx} /> {json.en_}</div>}
                                    {['0k', '00'].includes(scriptMode) && <div className="title-en_0"><ItemCnt idx={idx} /> {json.en_0}</div>}
                                    {['00', 'e0'].includes(scriptMode) && <div className="title-ko_0">{json.ko_0}</div>}
                                    {data.isSupportLang() && ['_k', 'ek', '0k'].includes(scriptMode) && <div className="title-ko">{json.ko}</div>}
                                </>
                        }

                    </div>
                    <div className="col-right">
                        {/* {idx} */}


                    </div>
                </div>
                <div className="item-bottom">
                    <div>
                        <div className="col">
                            <button onClick={() => {
                                data.setCurrPlayBtnState('stop', 'a3');// 현재 실행되고 있는 경우 하단 메인 컨트롤을 멈추기위해서
                                setTimeout(() => {
                                    // 1개 반복
                                    playOneItem();
                                })
                            }} className="btn">
                                <i className="fas fa-volume-up">
                                    <span className="i-label">듣기</span>
                                </i>
                            </button>
                            <button className="btn">
                                {!(sentenseRepeatIdx === idx) && <i onClick={() => {
                                    data.setCurrPlayBtnState('stop', 'a2');// 현재 실행되고 있는 경우 하단 메인 컨트롤을 멈추기위해서
                                    setTimeout(() => {
                                        temprepeatMode = fbase.userMoreInfo.repeatMode;
                                        fbase.userMoreInfo.repeatMode = '1';
                                        data.setCurrPlayBtnState('play');
                                        // // setIsRepeat(true);
                                        data.audioPlayPause(idx);
                                        // repeat equalizer보이기 인덱스
                                        setSentenseRepeatIdxState(idx);
                                    })
                                }} className="fas fa-retweet">
                                    <span className="i-label">무한반복</span>
                                </i>}
                                {sentenseRepeatIdx === idx && <div className="stop-repeat" onClick={() => {
                                    data.setCurrPlayBtnState('stop', 'a1');
                                    fbase.userMoreInfo.repeatMode = temprepeatMode;
                                    // setIsRepeat(false);
                                    // repeat equalizer보이기 인덱스
                                    setSentenseRepeatIdxState(9999);
                                }} >
                                    <Equalizer />
                                    <span className="i-label">반복종료</span>
                                </div>}

                            </button>
                            <button className="btn">
                                {isCheck
                                    ?
                                    <i className="fas fa-bookmark checkbox check" onClick={() => {
                                        checkClick(false);
                                    }}><span className="i-label">저장됨</span></i>
                                    :
                                    <i className="far fa-bookmark checkbox" onClick={() => {
                                        checkClick(true);
                                    }}><span className="i-label">문장저장</span></i>
                                }
                            </button>
                        </div>
                        <div className="col">
                            {(data.isSupportLang() && words) && <button className="btn btn-word" onClick={() => {
                                setIsOpenDesc(!isOpenDesc);
                            }}><i className="fas fa-plus">
                                    <span className="i-label">단어({words.length})</span></i>
                            </button>}

                            <button onClick={() => {
                                data.pageShadowingParam.sentenseEn = json.en;
                                data.pageShadowingParam.sentenseKo = json.ko;
                                data.pageShadowingParam.sentenseIdx = idx;
                                data.pageShadowingParam.topicID = clipID;
                                data.setCurrPlayBtnState('stop', 'a10');// 현재 실행되고 있는 경우 하단 메인 컨트롤을 멈추기위해서
                                // core.insertPage('PageShadowing');
                                setIsShowShadowing(true);

                            }} className="btn btn-sndtraining">
                                <span className="logo-snd">SD</span><span className="i-label">강화훈련</span>
                            </button>
                            {['_k', 'e0', '0k', '00'].includes(scriptMode) && <button className={classnames(["btn btn-glass", { glasson: isGlassMode }])} onClick={() => {

                                setIsGlassMode(true);
                                setTimeout(() => {
                                    setIsGlassMode(false);
                                }, 3000);
                            }}><i className="fas fa-eye i-con"><span className="i-label">잠깐보기</span></i>
                            </button>}
                        </div>
                    </div>
                    {isOpenDesc && words && <div>
                        <WordList json={words} />
                    </div>}
                </div>
            </div>
        </div >
    )
}

const WordList = ({
    json
}: {
    json: any
}) => {
    const Itm = json.map((item: string[], i: number) => {
        return <li className="item-word">
            <span className="word">{item[0]}</span>
            <span className="mean">{item[1]}</span>
        </li>
    })
    return (
        <ul className="items-word">
            {Itm}
        </ul>
    );
}

const ItemCnt = ({
    idx,
}: {
    idx: number,
}) => {
    return (
        <span className="item-cnt">
            {idx + 1}
        </span>
    )
}

const PlayerControl = ({
    topicID
}: {
    topicID: string
}) => {
    // alert(data.env.screenHeight);
    const [renderCnt, setRenderCnt] = useState(0);
    const [isClose, setIsClose] = useRecoilState(isCloseDetailPanelState);
    const [repeat, setRepeat] = useState(1);
    const [interval, setInterval] = useState(0);
    const [speed, setSpeed] = useState(1);
    const isFreeClip: boolean = data.isFreeClip(topicID);
    const isAllPass: boolean = fbase.userMoreInfo.buyCourses.length === 0;

    // console.log('tmplog_isFreeClip', isFreeClip, isAllPass, !isFreeClip && !isAllPass)
    // console.log('repeat,interval,speed', repeat, interval, speed);
    useEffect(() => {
        EventEmitter.subscribe(Event.PURCHASE_COMPLETE, () => {
            setRenderCnt(renderCnt + 1);
        });
    }, []);
    data.playerSetting = {
        repeat: repeat,
        interval: interval,
        speed: speed,
    }
    return (

        <div className={`PlayerControl`}>
            <div onClick={() => {
                if (isClose) {
                    setIsClose(!isClose);
                }
            }} className={classnames(["functions", { close: isClose }])}>
                <div className="center">
                    <div className="func-header">
                        <button className="btn btn-blank btn-open" onClick={() => {
                            setIsClose(!isClose);
                        }}>
                            {isClose && <>설정열기<i className="fas fa-chevron-up up"></i></>}
                            {!isClose && <>설정닫기<i className="fas fa-chevron-down down"></i></>}
                        </button>

                    </div>

                    <div className={`func-setting`}>
                        <div className="row">
                            <div className="col1">반복</div>
                            <div className="col2">
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: repeat === 1 }])}
                                    onClick={() => {
                                        setRepeat(1);
                                    }}>x1</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: repeat === 2 }])}
                                    onClick={() => {
                                        setRepeat(2);
                                    }}>x2</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: repeat === 3 }])}
                                    onClick={() => {
                                        setRepeat(3);
                                    }}>x3</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: repeat === 4 }])}
                                    onClick={() => {
                                        setRepeat(4);
                                    }}>x4</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: repeat === 5 }])}
                                    onClick={() => {
                                        setRepeat(5);
                                    }}>x5</button>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col1">속도</div>
                            <div className="col2">
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: speed === 1 }])}
                                    onClick={() => {
                                        setSpeed(1);
                                    }}>x1</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: speed === 0.9 }])}
                                    onClick={() => {
                                        setSpeed(0.9);
                                    }}>x0.9</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: speed === 0.8 }])}
                                    onClick={() => {
                                        setSpeed(0.8);
                                    }}>x0.8</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: speed === 0.7 }])}
                                    onClick={() => {
                                        setSpeed(0.7);
                                    }}>x0.7</button>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col1">간격</div>
                            <div className="col2">
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: interval === 0 }])}
                                    onClick={() => {
                                        setInterval(0);
                                    }}>0s</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: interval === 1 }])}
                                    onClick={() => {
                                        setInterval(1);
                                    }}>1s</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: interval === 1.5 }])}
                                    onClick={() => {
                                        setInterval(1.5);
                                    }}>1.5s</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: interval === 2 }])}
                                    onClick={() => {
                                        setInterval(2);
                                    }}>2s</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: interval === 2.5 }])}
                                    onClick={() => {
                                        setInterval(2.5);
                                    }}>2.5s</button>
                                <button
                                    className={classnames(["btn btn-blank btn-fc",
                                        { active: interval === 9999 }])}
                                    onClick={() => {
                                        setInterval(9999);
                                    }}>문장길이</button>

                            </div>
                        </div>

                    </div>
                    <div className="func-summary">
                        반복<span className="bold">x{repeat}</span>
                        속도<span className="bold">x{speed}</span>
                        간격<span className="bold">{(() => {
                            return interval === 9999 ? '문장길이' : interval + 's';
                        })()}</span>

                    </div>
                </div>

            </div>

            <div className="controller">

                <div className="col-left">
                    <div>
                        <BtnRepeatMode />
                        <BtnScriptMode />
                    </div>
                </div>

            </div>
        </div>
    )
}

// let repeatMode = 'ALL';
const BtnRepeatMode = () => {

    useEffect(() => {
        EventEmitter.subscribe(Event.CHANGE_BTN_PLAY_STATE, (sta: string) => {
            // alert(sta);
            if (sta === 'stop') {
                setIsDisable(true);
            } else {
                setIsDisable(false);
            }
        });
        EventEmitter.subscribe(Event.AUDIO_STOP, () => {

            // console.log('tmplog_currPlayBtnStateAUDIO_STOP', update);
            setUpdate(update + 1);
        })
    }, []);

    let dataRepeatMode = fbase.userMoreInfo.repeatMode;
    const [update, setUpdate] = useRecoilState(btnRepeatModeUpdateState);
    const [mode, setMode] = useState(dataRepeatMode);
    const [isDisable, setIsDisable] = useState(true);
    // 1개 반복은 개별의 반복으로 처리해서 메인컨트롤러 플레이는 아님
    const isPlay = (data.currPlayBtnState === 'play' && fbase.userMoreInfo.repeatMode !== '1');


    appBridge.consolelog('tmplog_currPlayBtnState2' + ', ' + isPlay + ', ' + update + ', ' + data.currPlayBtnState + ', ' + fbase.userMoreInfo.repeatMode)
    const setSentenseRepeatIdxState = useSetRecoilState(sentenseRepeatIdxState);

    return (
        <>
            <div className="BtnRepeatMode2">
                <button onClick={() => {
                    // 개별아이템 반복중일경우 tempRepeatMode처리
                    if (temprepeatMode !== 'nil') {
                        fbase.userMoreInfo.repeatMode = temprepeatMode;
                        temprepeatMode = 'nil';
                    }
                    fbase.userMoreInfo.repeatMode = 'ALL';
                    // 개별아이템 반복모드 켜진경우 중지 처리
                    setSentenseRepeatIdxState(9999);
                    if (isPlay) {
                        data.setCurrPlayBtnState('stop', 'BtnRepeatMode2');
                        EventEmitter.dispatch(Event.CHANGE_BTN_PLAY_STATE, "stop");
                        EventEmitter.dispatch(Event.SHOW_NOTI_MESSAGE, "멈춤");
                    } else {
                        data.setCurrPlayBtnState('play');
                        EventEmitter.dispatch(Event.CHANGE_BTN_PLAY_STATE, "play");
                        EventEmitter.dispatch(Event.SHOW_NOTI_MESSAGE, "실행");
                    }
                    setUpdate(update + 1);
                }} className={classnames(["btn btn-sound", { active: isPlay }])}>
                    {isPlay && <><Equalizer /><span className="i-label active">듣기중단</span></>}
                    {!isPlay && <i className="fas fa-volume-up"><span className="i-label">전체듣기</span></i>}
                </button>
                <button onClick={() => {
                    if (dataRepeatMode === 'ALL') {
                        fbase.userMoreInfo.repeatMode = '';
                    } else {
                        fbase.userMoreInfo.repeatMode = 'ALL'
                    }
                    setUpdate(update + 1);
                }} className={classnames(["btn btn-repeat", { active: dataRepeatMode === 'ALL' }])}>
                    <i className="fas fa-redo-alt"></i>
                </button>
            </div>

        </>
    )
}

const scriptModeStr: any = {
    // '_k': '_/한',
    '00': '.../...',
    'e0': '영/...',
    '0k': '.../한',
    'ek': '영/한',
}
const BtnScriptMode = () => {
    const [mode, setMode] = useState(scriptMode);

    return (
        <>
            <div className="BtnScriptMode2">
                <button onClick={() => {
                    scriptMode = 'ek';
                    setMode(scriptMode);
                    callModeChange();
                }} className={classnames(["btn btn-scriptmode", { active: scriptMode === 'ek' }])}>영+한</button>
                {data.isSupportLang() && <button onClick={() => {
                    scriptMode = 'e0';
                    setMode(scriptMode);
                    callModeChange();
                }} className={classnames(["btn btn-scriptmode", { active: scriptMode === 'e0' }])}>영어만</button>}
                <button onClick={() => {
                    scriptMode = '0k';
                    setMode(scriptMode);
                    callModeChange();
                }} className={classnames(["btn btn-scriptmode", { active: scriptMode === '0k' }])}>한글만 </button>
            </div>

        </>

    )
}