import React, { useEffect, useState } from 'react';
import { useRecoilValue } from 'recoil';
import data from '../core/Data';
import { cidState, tpidState } from '../core/store';
import PagePlaylist from '../PagePlaylist';
import WCourse from './WCourse';
// let json:any = '';
const selfName = "WPlayer";
const WPlayer = () => {
    const tpid = useRecoilValue(tpidState);
    const cid = useRecoilValue(cidState);
    const [isReadyJSON, setIsReadyJSON] = useState(false);
    useEffect(() => {
        // setTimeout(()=>{
        // console.log('makeCurrJSONWH tpidcid:',tpid,cid)
        data.makeCurrJSONWH(tpid, cid, (json: any) => {
            // json = json;
            data.currJSON = {
                id: tpid,
                json: json,
            }
            data.currPlaysTotal = data.currJSON.json.sentense.length;
            setIsReadyJSON(true);
        });
        // },1000)
        // console.log('tmplog_tpidciddd',tpid,cid)
    }, [tpid, cid])
    // debugger;
    console.log('data.leftJSON', data.leftJSON);
    const courseItemListJSON = data.leftJSON.filter((item: any, i: number) => {
        return item.courseID === cid;
    })
    return (
        <div className="WPlayer">

            {
                isReadyJSON
                    ?
                    <div className="contents">
                        <WCourse json={courseItemListJSON[0]} parentName={selfName} />
                        <PagePlaylist isShowPlayerControl={true} />
                    </div>
                    :
                    <div>Loading....</div>
            }
        </div>
    );
};

export default WPlayer;