import React from 'react';
import core from '../core/Core';
import data from '../core/Data';
import lang from '../core/i18n';
const AlertBuyProduct = ({
    openParam
}: {
    openParam: any
}) => {
    let price = '';
    let inappInfoArr = data.backInappInfo;
    let locale;
    let printPrice = '';
    if (inappInfoArr && inappInfoArr.length > 0) {
        const arr = inappInfoArr[0].split('#')
        price = arr[2];
        locale = arr[3];
        locale = locale.replace(/ \(fixed\)/, '');
        locale = locale.substring(locale.length - 3);

        if (locale === "KRW") {
            printPrice = `(${data.numberWithCommas(parseInt(price)) + "원"})`;
        } else {
            printPrice = `(${locale + price})`;
        }
    }




    const courseInfoJSON = data.leftJSON.filter((item: any, i: number) => {
        console.log('data.leftJSON', item.courseID, openParam.courseID)
        return item.courseID === openParam.courseID;
    })[0];
    console.log('data.leftJSON', data.leftJSON, courseInfoJSON)
    return (
        <div className="AlertNoticeInapp">
            <div className="message">
                구매상품:
                {courseInfoJSON.title}
                구매가격: {courseInfoJSON.prices[0]}
            </div>

            <button className="btn-confirm" onClick={() => {
                core.hideAlert();
                core.insertPage("PageMenu");
            }}>구매 진행</button>
            <button className="btn-close" onClick={() => {
                core.hideAlert();
            }}>그냥 닫기</button>
        </div>
    );
};

export default AlertBuyProduct;