    getRootPath() {
        if (this.rootPath !== '') return this.rootPath;
        let fullPath = (window as any).location.href;
        // 웹인경우 / 경로 
        if (fullPath.substr(0, 4) === 'http') {
            this.rootPath = '/';
            return this.rootPath;
        }
        // 앱인경우 index경로 
        this.rootPath = fullPath.split('index.html')[0];
        return this.rootPath;
        // alert(this.win.location.href);
    }

