    // topicID로  courceJSON에서 item 뽑기 
    getCourceItemToTopicID(topicID: string) {
        let result: any = null;
        const tabs = courseJSON.tabs;
        for (let i: number = 0; i < tabs.length; i++) {
            const tabsJSON = tabs[i];
            const tabsItems = tabsJSON.items;
            for (let j: number = 0; j < tabsItems.length; j++) {
                const item = tabsItems[j];
                if (item.aid === topicID) {

                    result = Object.assign(item, {
                        courseID: tabsJSON.courseID,
                        idx: j
                    });
                    break;
                }

            }
        }
        return result;
    }

