    makeCurrJSONFromClip(inType: 'clip' | 'topic', clipJSON: any, inCallback: () => void) {
        this.getTopicData(clipJSON.topicID, (json: any) => {
            this.makeMoreInfoJSON(json);
            // console.log('currJSONcurrJSON1', clipJSON)
            // console.log('currJSONcurrJSON2', this.currJSON)

            // 클립의 index 
            const filterIdxs: number[] = [];
            clipJSON.sentense.map((item: any) => {
                filterIdxs.push(item.idx);
            })

            // currJSON.해당 클립의 idx 만 필터링 
            // const copyCurrJSON = this.currJSON.slice(0);
            const filterSentense = this.currJSON.json.sentense.filter((item: any, i: number) => {
                // console.log('currJSONcurrJSON3', filterIdxs, i);
                return filterIdxs.includes(i);
            })
            this.currJSON.type = inType;
            this.currJSON.json.sentense = filterSentense;
            this.currPlaysTotal = this.currJSON.json.sentense.length;
            inCallback();
        });

    }

