    // 조회한 topic json 에 ...변환 속성도 추가 
    makeMoreInfoJSON(json: any): any {
        const convert0 = (str: string) => {
            let r = '';
            str.split('').map(s => {
                if (s === ' ') {
                    r += ' ';
                } else {
                    r += '.';//▒ ▤ ▥ ▨ ▧ ▦ ▩
                }
            })
            return r;
        }
        console.log('currJSONcurrJSON a1', json)

        const sentenseJSON = json.json.sentense.map((item: any, i: number) => {
            return {
                ...item,
                en_0: convert0(item.en),
                ko_0: convert0(item.ko),
            };
        });
        console.log('makeMoreInfoJSON', sentenseJSON);
        json.json.sentense = sentenseJSON;
        this.currJSON = json;
        // alert(this.currJSON.json.sentense.length);
        this.currPlaysTotal = this.currJSON.json.sentense.length;
        return json;
    }

