    setCurrPlayBtnState(state: 'play' | 'stop', desc = '') {
        console.log('setCurrPlayBtnState', state, desc);
        this.currPlayBtnState = state;
        if (state === 'play') {
            this.audioPlayPause(this.currPlayIdx);
        } else if (state === 'stop') {
            EventEmitter.dispatch(Event.AUDIO_STOP, {});
            EventEmitter.dispatch(Event.SENTENSE_PLAYING, 999);
        }

    }

    audioPlayRepeat(idx: number) {
        this.currPlayIdx = idx;
    }

    numberWithCommas = (x: any) => {
        let parts = x.toString().split(".");
        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        return parts.join(".");
    }


    audioPlayPause(idx: number, inCallback?: () => void) {
        console.log('tmplog_debugging a');
        if (this.currPlayIdx === idx) {
            this.playAudioCnt++;
        } else {
            this.playAudioCnt = 1;
        }
        this.currPlayIdx = idx;

        const t: string[] = this.currJSON.json.sentense[idx].timeen.split('|');

        if (t && t.length === 2) {
            console.log('tmplog_debugging a1');
            // sentense active
            EventEmitter.dispatch(Event.SENTENSE_PLAYING, this.currPlayIdx);
            EventEmitter.dispatch(Event.AUDIO_PLAY_PAUSE, {
                startTime: Number(t[0]),
                endTime: Number(t[1]),
                callback: () => {
                    console.log('tmplog_debugging a2');
                    if (inCallback) inCallback();

                    // sentense focus
                    EventEmitter.dispatch(Event.SENTENSE_PAUSE, this.currPlayIdx);

                    console.log('tmplog_debugging a3', this.currPlayBtnState);
                    // console.log('this.currPlayBtnState', this.currPlayBtnState, this.currPlaysTotal)
                    if (this.currPlayBtnState === 'play') {

                        setTimeout(() => {
                            let nextIdx: number = this.currPlayIdx + 1;
                            const repeatMode = fbase.userMoreInfo.repeatMode;
                            if (this.playerSetting.repeat > this.playAudioCnt) {
                                nextIdx = this.currPlayIdx;
                            } else if (repeatMode === 'ALL') {
                                nextIdx = this.currPlayIdx + 1;
                            } else if (repeatMode === '1') {
                                nextIdx = this.currPlayIdx;
                            }
                            console.log('tmplog_debugging a4', this.currPlayIdx, repeatMode, nextIdx, this.currPlaysTotal);

                            // alert('sss' + nextIdx + ',' + this.currPlaysTotal)

                            // 마지막 트랙 체크 
                            if (nextIdx > this.currPlaysTotal - 1) {

                                // 반복모드가 아니면 종료(더이상 반복안함)
                                if (repeatMode === '') {
                                    this.currPlayIdx = 0;
                                    // this.setCurrPlayBtnState('stop');
                                    // EventEmitter.dispatch(Event.SET_BTN_PLAY_STATE, 'stop');

                                    this.setCurrPlayBtnState('stop', 'a9');
                                    EventEmitter.dispatch(Event.CHANGE_BTN_PLAY_STATE, "stop");
                                    return;
                                }
                                // 헤더는 0으로 돌림 
                                nextIdx = 0;
                            }

                            this.audioPlayPause(nextIdx);
                        }, 300)

                    }
                },
            })
        }

    }

