    // 문장 또는 클립 저장 
    setSettingKeepClipSentense(
        type1: 'clip' | 'sentense',
        type2: 'add' | 'remove',
        value: any) {
        console.log('setSettingKeepClipSentense', type1, type2, value);
        let arr: string[] = [];
        let newArr: string[] = [];
        if (type1 === 'clip') {

            arr = fbase.userMoreInfo.keepClips;
        } else if (type1 === 'sentense') {
            arr = fbase.userMoreInfo.keepSentense;
        }
        if (arr.includes(value)) {
            // 포함되어있을때 삭제 케이스 
            if (type2 === 'remove') {
                // 삭제 
                newArr = arr.filter((item, i) => {
                    console.log('setSettingKeepClipSentense222', item, value)
                    return item !== value;
                });
                console.log('setSettingKeepClipSentense333', newArr)
            }
        } else {
            // 불포함인데 포함 케이스 
            if (type2 === 'add') {
                // 포함 
                arr.push(value)
                newArr = JSON.parse(JSON.stringify(arr));
            }
        }

        if (type1 === 'clip') {
            fbase.userMoreInfo.keepClips = newArr;
            console.log('tmplog_keep1', fbase.userMoreInfo.keepClips);
        } else if (type1 === 'sentense') {
            fbase.userMoreInfo.keepSentense = newArr;
            console.log('tmplog_keep2', fbase.userMoreInfo.keepSentense);
        }
        fbase.syncMoreInfo();
        // appBridge.syncStorageData();

    }

    utils = {
        today: () => {
            return new Date();
            // return new Date('2020-07-19');
        },
        todayISO: () => {
            const timezoneOffset = new Date().getTimezoneOffset() * 60000;
            return new Date(Date.now() - timezoneOffset);

        },
        isSameDay: (day1: Date, day2: Date) => {

            // appBridge.consolelog('tmplog_playcnt3samefc:' + day1.toISOString().substr(0, 10)
            //     + ',' +
            //     day2.toISOString().substr(0, 10)
            //     + ',' +
            //     day1
            //     + ',' +
            //     day2

            // )

            return day1.toISOString().substr(0, 10) === day2.toISOString().substr(0, 10);

        },
        // 시작날짜와 오늘날짜 그리고 총 상품 기간을 기준으로 남은 날짜 계산
        getLiftDate: (startDate: Date, dateLen: number) => {
            console.log('getLiftDate',
                this.utils.diffDays(startDate, this.utils.today()),
                startDate,
                dateLen,
                this.utils.today());
            return dateLen - (this.utils.diffDays(startDate, this.utils.today()));
        },
        getAfterDay: (baseDate: Date, addDay: number) => {
            return this.utils.getDateStr(new Date(baseDate.getTime() + (addDay * 24 * 60 * 60 * 1000))).substr(0, 10);
        },
        diffDays: (date: Date, otherDate: Date) => {
            return Math.ceil(Math.abs(date.getTime() - otherDate.getTime()) / (1000 * 60 * 60 * 24));
        },
        getDateStr: (d: Date) => {
            return `${d.getFullYear()}-${_padStart((d.getMonth() + 1) + '', 2, '0')}-${_padStart(d.getDate() + '', 2, '0')}:${_padStart(d.getHours() + '', 2, '0')}-${_padStart(d.getMinutes() + '', 2, '0')}`
        },

        getTodayStr: () => {

            return this.utils.getDateStr(this.utils.today());
        },
        getKUID: () => {
            const uu = 'Uxyxxxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
                var r = Math.random() * 16 | 0,
                    v = c == 'x' ? r : (r & 0x3 | 0x8);
                return v.toString(16);
            });
            return uu.substr(0, 1) + uu.substring(1);
        }
    }

