import React, { useState, useEffect } from 'react';
import './scss/Main.scss';
import Route from './views/Route';
import Loading from './views/component/Loading';
import EventEmitter, { Event } from './views/core/EventEmitter';
import { isLoadingState } from './views/core/store';
import { useSetRecoilState } from 'recoil';
import Iframe from 'react-iframe';
import appBridge from './views/core/AppBridge';
import WHome from './views/wh/WHome';

function App() {
  const [isAppReady, setIsAppReady] = useState(false);
  const setIsLoading = useSetRecoilState(isLoadingState);

  useEffect(() => {
    EventEmitter.subscribe(Event.IS_APP_READY, () => {
      // alert('tmplog IS_APP_READY1')
      setIsLoading(false);
      setIsAppReady(true);
    });
  }, []);
  // alert('tmplog IS_APP_READY2' + isAppReady)
  return (
    <div className="App">
      <WHome />
      {/* <Loading />
      {isAppReady && <Route />} */}
    </div>
  );
}

export default App;
