import React from 'react';
import core from '../core/Core';
import { padStart as _pad } from 'lodash';
const WCourseItem = ({
    viewType,
    itemJSON,
    courseID,
    idx,
}: {
    viewType: string,
    itemJSON: any,
    courseID: string,
    idx: number,
}) => {
    // console.log('WCourseItem',viewType,itemJSON,idx);
    if (viewType === 'movie') {
        console.log('WCourseItem', itemJSON.tit);
    }
    return (
        <>
            {
                viewType === 'movie'
                    ?
                    <CItemMovie courseID={courseID} itemJSON={itemJSON} idx={idx} key={idx} />
                    :
                    <CItem courseID={courseID} itemJSON={itemJSON} idx={idx} key={idx} />
            }
            {/* <CItem itemJSON={itemJSON} idx={idx} key={idx} /> */}
        </>
    );
};
const CItem = (
    {
        itemJSON,
        courseID,
        idx
    }: {
        itemJSON: any,
        courseID: string,
        idx: number
    }) => {
    return (
        <li className="CourseItem" onClick={() => {
            core.setParameter(itemJSON.query);
        }} key={idx}>{itemJSON.tit}</li>
    );
}
const CItemMovie = (
    {
        itemJSON,
        courseID,
        idx
    }: {
        itemJSON: any,
        courseID: string,
        idx: number
    }) => {
    const Tits = itemJSON.tit.map((str: string, i: number) => {
        return <div className="sentense">{i + 1}. {str}</div>
    })
    const idxPad = _pad(String(idx + 1), 3, '0');
    return (
        <li className="CourseItem movie" onClick={() => {
            // alert(itemJSON.query);
            core.setParameter(itemJSON.query);
        }} key={idx}>
            <div className="group-img">
                <img className="img" src={`/resource/courses/${courseID}/thumb/a${idxPad}.jpg`}></img>
                <div className="sceneno">
                    <div className="t1">scene</div>
                    <div className="t2">{idxPad}</div>
                </div>
            </div>

            <div className="sentenses">{Tits}</div>
        </li>
    );
}
export default WCourseItem;