import React, { useEffect, useState } from 'react';
import EventEmitter, { Event } from '../core/EventEmitter';
import fbase, { authService } from './fbase';
// let tempLoginPopupParam: {
//     from: string,
//     courseID: string,
// } | null = null;
const FBViewLogout = () => {
    const [inputEmail, setInputEmail] = useState('');
    const [inputPw, setInputPw] = useState('');
    const [inputPw2, setInputPw2] = useState('');
    const [newAccount, setNewAccount] = useState(false);
    const [isShowLoginPopup, setisShowLoginPopup] = useState(false);
    useEffect(() => {
        EventEmitter.subscribe(Event.FB_START_LOGIN, (options: any) => {
            // tempLoginPopupParam = options;
            setisShowLoginPopup(true);
        })
    }, []);
    const onChange = (event: any) => {
        const {
            target: { name, value },
        } = event;
        if (name === "email") {
            setInputEmail(value);
        } else if (name === "pw") {
            setInputPw(value);
        } else if (name === "pw2") {
            setInputPw2(value);
        }
    }
    const onSubmit = async (e: any) => {
        e.preventDefault();
        // 신규가입인경우 비번 두번입력 비교
        if (newAccount && (inputPw !== inputPw2)) {
            alert(fbase.convertErrorMessage({
                code: 'auth/invalidNewPw'
            }));
            return;
        }
        try {
            //let data;
            if (newAccount) {
                // create account
                await authService.createUserWithEmailAndPassword(inputEmail, inputPw);
            } else {
                // log in
                await authService.signInWithEmailAndPassword(inputEmail, inputPw);
            }

        } catch (error) {
            //   setError(error.message);
            console.log(error);
            alert(fbase.convertErrorMessage(error));
            // alert(error.message);
        }
    }

    // if (!isShowLoginPopup) {
    //     if (tempLoginPopupParam !== null && tempLoginPopupParam.from === 'buyProduct') {
    //         EventEmitter.dispatch(Event.FB_START_LOGIN, {
    //             from: "buyProduct",
    //             courseID: tempLoginPopupParam.courseID,
    //         });
    //     }
    //     tempLoginPopupParam = null;
    // }
    return (
        <div className="FBViewLogout">
            <div>
                <button onClick={() => {
                    setisShowLoginPopup(true);
                }} className="btn"><i className="fas fa-user-circle">로그인</i></button>
            </div>
            {
                isShowLoginPopup &&
                <div className="popup-login">
                    <div className="popup-login-in">
                        <div className="popup-header">
                            <div className="left">&nbsp;</div>

                            <h3 className="center">
                                {newAccount ? '회원가입' : '로그인'}
                            </h3>
                            <div className="right">
                                <button onClick={() => {
                                    setisShowLoginPopup(false);
                                }} className="btn btn-close"><i className="fas fa-times"></i></button>
                            </div>
                        </div>

                        <form onSubmit={onSubmit}>
                            <table>
                                <tbody>
                                    <tr>
                                        <td>이메일</td>
                                        <td>
                                            <input name="email" type="text" onChange={onChange} value={inputEmail}></input>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>비밀번호</td>
                                        <td>
                                            <input name="pw" type="password" onChange={onChange} value={inputPw}></input>
                                        </td>
                                    </tr>
                                    {
                                        newAccount && <>
                                            <tr>
                                                <td>비밀번호 확인</td>
                                                <td>
                                                    <input name="pw2" type="password" onChange={onChange} value={inputPw2}></input>
                                                </td>
                                            </tr>
                                        </>
                                    }
                                </tbody>
                            </table>
                            <button>{newAccount ? '가입신청' : '로그인'}</button>
                        </form>
                        {
                            newAccount
                                ?
                                <button onClick={() => {
                                    setNewAccount(false);
                                }}>이미 회원인가요?(로그인 하러가기)</button>
                                :
                                <>
                                    <button onClick={() => {
                                        // setNewAccount(true);
                                        fbase.sendPasswordResetEmail(inputEmail);
                                    }}>비밀번호를 잊으셨나요?</button><br></br>
                                    <button onClick={() => {
                                        setNewAccount(true);
                                    }}>아직 회원이 아닌가요?(회원가입 하러가기)</button>
                                </>
                        }
                    </div>

                </div>
            }


        </div>
    );
};

export default FBViewLogout;